@mixin cocogoose-tx ($size, $weight, $height) {
    font-family: "COCOGOOSE", sans-serif;
    font-size: $size;
    font-weight: $weight;
    line-height: $height;   
}
@mixin layout($direction, $align, $justify) { 
    display: flex;
    flex-direction: $direction;
    align-items: $align;
    justify-content: $justify;
}

@mixin lat-tx ($size, $weight, $height){
    font-family: "Lato", sans-serif;
    font-size: $size;
    font-weight: $weight;
}