@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700;900&display=swap");
footer {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  margin: 25px 10px;
}
footer p {
  font-family: "Lato", sans-serif;
  font-size: 12px;
  font-weight: 500;
  color: #272a31;
}
footer img {
  filter: grayscale(0.9);
  width: 160px;
}

.hero {
  background: url("../img/bg.jpg");
}

header {
  position: fixed;
  background: url("../img/bg.jpg");
  width: 100%;
  top: 0;
  padding: 25px;
  z-index: 10;
}
header nav svg {
  height: 25px;
  width: 25px;
}

*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

hr {
  margin-top: 24px;
  border: 1px solid #ec5242;
  width: 50%;
  margin-inline: auto;
}

body {
  width: 100%;
}

ul,
li {
  padding: 0;
  list-style: none;
}

@font-face {
  font-family: "COCOGOOSE";
  src: url("../../../dist/assets/fonts/Cocogoose_trial.eot") format("eot"), url("../../../dist/assets/fonts/Cocogoose_trial.woff") format("woff"), url("../../../dist/assets/fonts/Cocogoose_trial.ttf") format("truetype"), url("../../../dist/assets/fonts/Cocogoose_trial.svg#COCOGOOSE") format("svg");
  font-weight: normal;
  font-style: normal;
}
.headline {
  padding: 80px 25px;
  font-family: "Lato", sans-serif;
  font-size: 25px;
  font-weight: 600;
  color: #ec5242;
}
.headline p {
  font-size: 16px;
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  background-clip: content-box;
  background-image: url("../img/bg-txt.jpg");
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.headline .headline-box {
  background-color: #f7f7f7;
  border: 1px solid white;
  padding: 20px;
}
.headline .headline-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
.headline .date-event {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  color: #272a31;
  margin: 25px 0;
}

.partners-logo {
  background-color: #272a31;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: "Lato", sans-serif;
  font-size: 20px;
  font-weight: 500;
  padding: 50px 25px;
  text-align: center;
  color: white;
}
.partners-logo .wrap-logos ul {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 12px;
  flex-wrap: wrap;
}
.partners-logo img {
  width: 90px;
  margin: 20px 10px;
  filter: grayscale(1);
}

.headline {
  text-align: center;
}
.headline p {
  font-size: 16px;
}
.headline h1 {
  font-family: "COCOGOOSE", sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 24px;
  text-transform: uppercase;
  background-clip: content-box;
  background-image: url("../img/bg-txt.jpg");
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.headline .about-box {
  background-color: white;
  border: 1px solid #d3d3d3;
  padding: 20px;
  text-align: center;
  margin: 15px 5px;
}
.headline .about-box p {
  color: #272a31;
  font-size: 14px;
  line-height: 20px;
}
.headline .about-headline-contact {
  font-family: "Lato", sans-serif;
  font-size: 15px;
  font-weight: 500;
  color: #272a31;
  margin: 25px 0;
}
.headline .about-headline-contact strong {
  text-decoration: underline;
}

.course-logo {
  padding: 20px 25px;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
}
.course-logo p {
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}
.course-logo img {
  padding: 15px 34px;
  border: 1px solid #d3d3d3;
  filter: grayscale(0.9);
}

.last-events {
  padding: 20px 25px;
  font-family: "Lato", sans-serif;
  font-size: 24px;
  font-weight: 600;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.last-events .last-events-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
}
.last-events .last-events-wrap .events {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  height: 250px;
  width: 100%;
  background-color: #ec5242;
}
.last-events p {
  font-size: 15px;
  font-weight: 500;
  line-height: 20px;
}

section {
  margin: 35px 0;
}

hr {
  width: 10%;
}